import React from 'react';

import { DataGrid } from '@material-ui/data-grid';
import Button from '@material-ui/core/Button';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);


const MCCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.common.black,
      color: '#f44336',
    },
    body: {
      fontSize: 14,
      backgroundColor: '#f44336'
    },
  }))(TableCell);

  const WinnerCell = withStyles((theme) => ({
      head: {
        backgroundColor: theme.palette.common.black,
        color: '#ffef62',
      },
      body: {
        fontSize: 14,
        backgroundColor: '#ffef62'
      },
    }))(TableCell);
  

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);


function process(data) {
    console.log(data);
    var players = [
        {name: 'Tyler', G1: 'Scottie Scheffler', G2:'Tom Kim'},
        {name: 'Phil', G1: 'Rory McIlroy', G2:'Min Woo Lee'},
        {name: 'Joe', G1: 'Collin Morikawa', G2:'Jordan Spieth'},
        {name: 'Jordan', G1: 'Bryson DeChambeau', G2:'Patrick Cantlay'},
        {name: 'Jake', G1: 'Xander Schauffele', G2:'Sam Burns'},
        {name: 'Nate', G1: 'Brooks Koepka', G2:'Tyrrell Hatton'},
        {name: 'Craig', G1: 'Viktor Hovland', G2:'Sungjae Im'},
        {name: 'Terry', G1: 'Ludvig Ã…berg', G2:'Sepp Straka'},
        {name: 'Mike', G1: 'Max Homa', G2:'Wyndham Clark'},
        {name: 'Binh', G1: 'Hideki Matsuyama', G2:'Matthew Fitzpatrick'},
        {name: 'Amy', G1: 'Tommy Fleetwood', G2:'Justin Thomas'},
        {name: 'Colin', G1: 'Sahith Theegala', G2:'Tony Finau'},
        {name: 'Aaron', G1: 'Cameron Smith', G2: 'Will Zalatoris'}
    ];

    var dict = {};
    data.standings.map(x => dict[x.player.firstName + ' ' + x.player.lastName] = x )
    players.forEach(function (x) {
        var g1info = dict[x.G1];
        var g2info = dict[x.G2];
        x.G1R = g1info.position.displayValue;
        x.G1Score = g1info.toPar.displayValue + ' (' + g1info.holesThrough.displayValue + ')';
        x.G2R = g2info.position.displayValue;
        x.G2Score = g2info.toPar.displayValue + ' (' + g2info.holesThrough.displayValue + ')';
        x.G1Calc = parseInt(g1info.position.displayValue.replace('T',''))
        x.G2Calc = parseInt(g2info.position.displayValue.replace('T',''))
        console.log(g1info);
        console.log(g2info);
        if(g1info.position.displayValue === 'MC') {
            x.G1Score = 'MC';
            x.G1R = g1info.roundScores[1].score.value;
            x.G1Calc = parseInt(x.G1R)
        }

        if(g2info.position.displayValue === 'MC') {
            x.G2Score = 'MC';
            x.G2R = g2info.roundScores[1].score.value;
            x.G2Calc = parseInt(x.G2R)
        }

        if(g2info.position.displayValue === "WD") {
            console.log(x)
            x.G2R = 90
            x.G2Score = 'WD'
            x.G2Calc = 90
        }
        
        x.AllUpScore = x.G1Calc + x.G2Calc
        
      });

      return players.sort((a, b) => (a.AllUpScore > b.AllUpScore) ? 1 : -1);
}

class myComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {leaderboard: null};  
    }

    componentDidMount() {
        const apiUrl = 'https://ace-api.usga.org/scoring/v1/leaderboard.json?championship=uso&championship-year=2024';
        fetch(apiUrl)
            .then((response) => response.json())
            .then((data) => this.setState({leaderboard: process(data)}));
    }
    
    render() {
        
        if(this.state.leaderboard === null) {
            return <h1>Loading...</h1>
        }
        
       // const classes = useStyles();
        return  <TableContainer component={Paper}>
        <Table aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell></StyledTableCell>
              <StyledTableCell align="center">Overall Score</StyledTableCell>
              <StyledTableCell align="left">Golfer 1</StyledTableCell>
              <StyledTableCell align="center">Score</StyledTableCell>
              <StyledTableCell align="center">Rank</StyledTableCell>
              <StyledTableCell align="left">Golfer 2</StyledTableCell>
              <StyledTableCell align="center">Score</StyledTableCell>
              <StyledTableCell align="center">Rank</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {this.state.leaderboard.map((row) => (
              <StyledTableRow key={row.name}>
                {row.G1R === '1' || row.G1R === 'T1' || row.G2R === 'T1' || row.G2R === '1' ? <WinnerCell component="th" scope="row">
                  {row.name}
                </WinnerCell> : <StyledTableCell component="th" scope="row">
                  {row.name}
                </StyledTableCell>}
                <StyledTableCell align="center">{row.AllUpScore}</StyledTableCell>
                {row.G1Score === 'MC' ? <MCCell align="left">{row.G1}</MCCell> : <StyledTableCell align="left">{row.G1}</StyledTableCell>}
                <StyledTableCell align="center">{row.G1Score}</StyledTableCell>
                <StyledTableCell align="center">{row.G1R}</StyledTableCell>
                {row.G2Score === 'MC' || row.G2Score === 'WD' ? <MCCell align="left">{row.G2}</MCCell> : <StyledTableCell align="left">{row.G2}</StyledTableCell>}
                <StyledTableCell align="center">{row.G2Score}</StyledTableCell>
                <StyledTableCell align="center">{row.G2R}</StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
        
    }
}
export default myComponent;